import React from 'react';
import classes from "./SelectionCircle.module.css"
import SelectionUnit from '../SelectionUnit/SelectionUnit';
import { useState } from 'react';

const SelectionCircle = ({ selectionUnits }) => {

    const [smallCircleStyle, setSmallCircleStyle] = useState({ backgroundColor: "white", color: "black" });
    const [numberText, setNumberText] = useState("ЦУР")
    const [titleText, setTitleText] = useState("Выберите ЦУР")


    selectionUnits = [{ angle: 10.592, imageSrc: process.env.PUBLIC_URL + "images/unit1.png", imageAlt: "unit1", color: "#FD483F", title: "Ликвидация нищеты" },
    { angle: 31.768, imageSrc: process.env.PUBLIC_URL + "images/unit2.png", imageAlt: "unit2", color: "#FCB864", title: "Ликвидация голода" },
    { angle: 52.944, imageSrc: process.env.PUBLIC_URL + "images/unit3.png", imageAlt: "unit3", color: "#729D6B", title: "Хорошее здоровье и благополучие" },
    { angle: 74.12, imageSrc: process.env.PUBLIC_URL + "images/unit4.png", imageAlt: "unit4", color: "#EA402F", title: "Качественное образование" },
    { angle: 95.296, imageSrc: process.env.PUBLIC_URL + "images/unit5.png", imageAlt: "unit5", color: "#FD5832", title: "Гендерное равенство" },
    { angle: 116.472, imageSrc: process.env.PUBLIC_URL + "images/unit6.png", imageAlt: "unit6", color: "#7BB7FF", title: "Чистая вода и санитария" },
    { angle: 137.648, imageSrc: process.env.PUBLIC_URL + "images/unit7.png", imageAlt: "unit7", color: "#FDC963", title: "Недорогостоящая и чистая энергия" },
    { angle: 158.824, imageSrc: process.env.PUBLIC_URL + "images/unit8.png", imageAlt: "unit8", color: "#B7323F", title: "Достойная работа и экономический рост" },
    { angle: 180, imageSrc: process.env.PUBLIC_URL + "images/unit9.png", imageAlt: "unit9", color: "#FC7942", title: "Индустриализация, инноватика и инфраструктура" },
    { angle: -158.824, imageSrc: process.env.PUBLIC_URL + "images/unit10.png", imageAlt: "unit10", color: "#FA4A68", title: "Уменьшение неравенства" },
    { angle: -137.648, imageSrc: process.env.PUBLIC_URL + "images/unit11.png", imageAlt: "unit11", color: "#FDA85D", title: "Устойчивые города и населенные пункты" },
    { angle: -116.472, imageSrc: process.env.PUBLIC_URL + "images/unit12.png", imageAlt: "unit12", color: "#CF9854", title: "Ответственное потребление и производство" },
    { angle: -95.296, imageSrc: process.env.PUBLIC_URL + "images/unit13.png", imageAlt: "unit13", color: "#56725F", title: "Борьба с изменением климата" },
    { angle: -74.12, imageSrc: process.env.PUBLIC_URL + "images/unit14.png", imageAlt: "unit14", color: "#6A98EA", title: "Сохранение морских экосистем" },
    { angle: -52.944, imageSrc: process.env.PUBLIC_URL + "images/unit15.png", imageAlt: "unit15", color: "#7FAE76", title: "Сохранение экосистем суши" },
    { angle: -31.768, imageSrc: process.env.PUBLIC_URL + "images/unit16.png", imageAlt: "unit16", color: "#4B68AF", title: "Мир, правосудие и эффективные институты" },
    { angle: -10.592, imageSrc: process.env.PUBLIC_URL + "images/unit17.png", imageAlt: "unit17", color: "#363E6C", title: "Партнерство в интересах устойчивого развития" },
    ];

    return (
        <div className={classes.SelectionCircle}>
            {selectionUnits.map(({ angle, imageSrc, imageAlt, color, title }, index) => {
                return <SelectionUnit angle={angle} imageSrc={imageSrc} imageAlt={imageAlt} color={color} title={title} index={index} key={imageAlt}
                    setSmallCircleStyle={setSmallCircleStyle} setNumberText={setNumberText} setTitleText={setTitleText} />
            })}
            <div className={classes.SmallCircle} style={smallCircleStyle}>
                <div className={classes.Text}>
                    <div className={classes.NumberText}>
                        {numberText}
                    </div>

                    <div className={classes.TitleText}>
                        {titleText}
                    </div>
                </div>
                <div className={classes.Title}></div>
            </div>
        </div>
    );
};

export default SelectionCircle;