import React, { useState } from 'react';
import classes from './Admin.module.css'
import Select from '../../UI/Select/Select';
import ButtonContainer from '../../UI/ButtonContainer/ButtonContainer';
import Container from '../../UI/Container/Container';
import GreenButton from '../../UI/GreenButton/GreenButton';
import RedButton from '../../UI/RedButton/RedButton';
import Input from '../../UI/Input/Input';
import Table from '../../UI/Table/Table/Table';

const curs = [1, 2, 3]
const tables = ["table1", "table2", "table3"];


const Admin = () => {
    const [data, setData] = useState([
        ["Год", "А", "Б", "В"],
        ["a", "1", "2", "3"],
        ["b", "4", "5", "6"]
    ]);

    return (
        <div className={classes.Admin}>
            <div className={classes.TitleContainer}>
                <h1 className={classes.Title}>Панель управления сервисом</h1>
            </div>
            <Select text={`База данных ЦУР для редактирования`} title={"Выберите раздел ЦУР"} options={curs} currentIndex={0} className={classes.CurSelector} />
            <Select text={`Таблица из выбранного ЦУР`} title={"Выберите таблицу"} options={tables} currentIndex={0} className={classes.TableSelector} />
            <ButtonContainer className={classes.ResetTable} text={"Сбросить текущую таблицу"}>ВОССТАНОВИТЬ ЗНАЧЕНИЯ</ButtonContainer>
            <ButtonContainer className={classes.NewTable} text={"Добавить новую таблицу в текущий ЦУР"}>ДОБАВИТЬ ТАБЛИЦУ</ButtonContainer>
            <ButtonContainer className={classes.DeleteTable} text={"Удалить текущую таблицу"}>УДАЛИТЬ ТАБЛИЦУ</ButtonContainer>
            <RedButton className={classes.Deny}>ОТМЕНИТЬ ИЗМЕНЕНИЯ</RedButton>
            <GreenButton className={classes.Apply}>ПРИМЕНИТЬ ИЗМЕНЕНИЯ</GreenButton>
            <Container className={classes.TableContainer}>
                <Input className={classes.TableName} placeholder={"Введите название таблицы"} text={"Название таблицы"} />
                <Table data={data} setData={setData} className={classes.Table}/>
                <ButtonContainer className={classes.AddRow} text={"Добавить строку в конец таблицы"}>ДОБАВИТЬ СТРОКУ</ButtonContainer>
                <ButtonContainer className={classes.DeleteRow} text={"Удалить строку в конце таблицы"}>УДАЛИТЬ СТРОКУ</ButtonContainer>
                <ButtonContainer className={classes.AddColumn} text={"Добавить столбец в конец таблицы"}>ДОБАВИТЬ СТОЛБЕЦ</ButtonContainer>
                <ButtonContainer className={classes.DeleteColumn} text={"Удалить столбец в конце таблицы"}>УДАЛИТЬ СТОЛБЕЦ</ButtonContainer>
            </Container>
        </div>
    );
};

export default Admin;