import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import classes from './Cur.module.css'
import Select from '../../UI/Select/Select';
import Input from '../../UI/Input/Input';
import GreenButton from '../../UI/GreenButton/GreenButton';
import Container from '../../UI/Container/Container';
import LineChart from '../../UI/LineChart/LineChart';

const Cur = () => {
    const tables = ["table1", "table2", "table3"];
    const goals = ["goal1", "goal2", "goal3"];
    const models = ["model1", "model2", "model3"];

    const [currentModel, setCurrentModel] = useState("");
    const [currentAccuracy, setCurrentAccuracy] = useState("");

    const [chartData, setChartData] = useState({
          labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
          datasets: [
            {
              label: 'My First Dataset',
              data: [65, 59, 80, 81, 56, 55, 40],
              fill: false,
              backgroundColor: 'rgb(75, 192, 192)',
              borderColor: 'rgba(75, 192, 192, 0.2)',
            },
          ],
        });

    const params = useParams();

    return (
        <div className={classes.Cur}>
            <div className={classes.TitleContainer}>
                <h1 className={classes.Title}>Прогноз значений ЦУР</h1>
            </div>
            <div className={classes.TitleContainer}>
                <h1 className={classes.Name}>Прогноз значений ЦУР</h1>
            </div>
            <Select text={`Таблица из перечня ЦУР ${parseInt(params.id) + 1}`} title={"Выберите таблицу"} options={tables} currentIndex={0} className={classes.TableSelector}/>
            <Select text={`Цель из перечня таблицы`} title={"Выберите цель"} options={goals} currentIndex={0} className={classes.GoalSelector}/>
            <Select text={`Модель машинного обучения`} title={"Выберите модель"} options={models} currentIndex={0} className={classes.ModelSelector}/>
            <Input placeholder={"Введите горизонт"} text={"На сколько лет вперед прогнозировать"} className={classes.InputHorizon}/>
            <GreenButton className={classes.Button}>РАССЧИТАТЬ ЗНАЧЕНИЯ</GreenButton>
            <Container className={classes.PlotContainer}>
                <div className={classes.TextModel}>
                    Выбранная модель: <span className={classes.ValueText}>{currentModel}</span>
                </div>
                <div className={classes.TextAccuracy}>
                    Точность: <span className={classes.ValueText}>{currentAccuracy}</span>
                </div>
                <LineChart className={classes.LineChar} chartTitle={"Название цели"} chartData={chartData}/>
            </Container>
        </div>
    );
};

export default Cur;