import React from 'react';
import TableCell from '../TableCell/TableCell';
import classes from './TableRow.module.css'

const TableRow = ({ rowIndex, globalData, setGlobalData, className }) => {
    return (
        <div className={classes.TableRow}>
            <TableCell rowIndex={rowIndex} columnIndex={0} globalData={globalData} setGlobalData={setGlobalData} className={className} isDisabled={true} />
            {
                globalData[rowIndex].slice(1).map((_, index) =>
                    <div>
                        <TableCell rowIndex={rowIndex} columnIndex={index + 1} globalData={globalData} setGlobalData={setGlobalData} key={index} className={className} />
                    </div>)
            }
        </div>
    );
};

export default TableRow;